.App {
  min-height: 100vh;
  position: absolute;
  box-sizing: border-box;


  .data {
    max-width: 56%;
    min-height: 100vh;
    position: relative;
    overflow-x: hidden;
    transition: all .5s ease;
    //@media screen and (max-height: 1041px) {
    //  width: 67%;
    //}
  }

  .image {
    max-width: 44%;
    min-height: 100vh;
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    border-left: 1px solid rgba(255, 255, 255, .2);
    transition: all .5s ease;
    box-sizing: border-box;
    //@media screen and (max-height: 1041px) {
    //  width: 33%;
    //}
  }
}

@media (max-height: 900px) {
  .data {
    width: 67%;
  }
  .image {
    width: 33%;
  }
}



